<template>
    <div class="step3">
        <el-col>
            <el-select id="select" :class="{inputs: true, warning: elProd}" v-model="value" placeholder="Escolha" :loading="loading">
                <el-option v-for="item in outros" :label="item.name" :id="'o' + item.id" :value="item"></el-option>
            </el-select>
        </el-col>
        <el-col>
            <el-input-number :class="{inputs: true, warning: elQtde}" v-model="qtde" :min="0" :max="10" ></el-input-number>
        </el-col>
        <el-col>
            <el-button type="success" @click="add">Adicionar</el-button>
        </el-col>
        <el-col>
            <el-table height="150" border :data="tableData" @row-click="apagarItem">
                <el-table-column prop="produto" label="Produto" width="100" align="left">
                </el-table-column>
                <el-table-column prop="qtde" label="Qtde" width="80" align="right">
                </el-table-column>
                <el-table-column prop="unit" label="PU" width="80" align="right">
                </el-table-column>
            </el-table>
        </el-col>
    </div>
</template>
<script>
    export default {
        props: ['outros'],
        data() {
            return {
                tableData: [],
                value: '',
                qtde: 0,
                loading: false,
                elQtde: false,
                elProd: false
            }
        },
        methods: {
            add() {
                if (this.value) {
                    this.elProd = false;

                    if (this.qtde) {
                        const pedido = {produto: this.value.name, qtde: this.qtde, unit: this.value.price};
                        this.tableData.unshift(pedido);
                        this.$store.dispatch('addListaPedidos', pedido);
                        this.elQtde = false;
                    } else {
                        this.$notify({
                            title: 'Aviso',
                            message: 'Quantidade inválida!',
                            type: 'warning'
                        });
                        this.elQtde = true;
                    }
                } else {
                    this.$notify({
                        title: 'Aviso',
                        message: 'Nenhum produto selecionado!',
                        type: 'warning'
                    });
                    this.elProd = true;
                }
            },
            apagarItem(row, event, column) {
                this.$confirm('Deseja apagar este lanche?', 'Aviso', {
                    confirmButtonText: 'Sim',
                    cancelButtonText: 'Não',
                    type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'info',
                        message: 'Produto apagado'
                    });
                    const index = this.tableData.indexOf(row);
                    this.$store.dispatch('removeListaPedidos', this.tableData[index]);
                    this.tableData.splice(index, 1);
                    
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: 'Produto não apagado'
                    });
                });
            }
        }
    }
</script>
<style scoped>
    .inputs {
        width: 80%;
    }
    .step3 {
        display: flex;
        flex-direction: column;
        text-align: center;
    }
    .step3 > div {
        margin: 15px 0;
    }
</style>


