<template>
    <div>
       <el-col>
            <el-table height="250" border :data="tableData" @row-click="apagarItem">
                <el-table-column prop="produto" label="Produto" width="100" align="left">
                </el-table-column>
                <el-table-column prop="qtde" label="Qtde" width="80" align="right">
                </el-table-column>
                <el-table-column prop="unit" label="PU" width="80" align="right">
                </el-table-column>
            </el-table>
            <h3 class="fullPrice" >Preço total: R$ {{fullPrice}}</h3>
        </el-col>
        
    </div>
</template>
<script>
    export default {
        data() {
            return {
                tableData: this.$store.getters.getListaPedidos,
                fullPrice: ''
            }
        },
        watch: {
            tableData() {
                this.fullPrice = this.calculateFullPrice();
            }
        },
        mounted() {
            this.fullPrice = this.calculateFullPrice();
        },
        methods: {
            calculateFullPrice() {
                const data = this.tableData;
                let price = 0.0;
                data.forEach((element) => {
                    price += element.unit * element.qtde;
                });
                /*
                for (let item of data) {
                    const unit = item.unit;
                    const qtde = item.qtde;
                    price += unit * qtde;
                }*/
                return price;
            }
        }
    }
</script>
<style scoped>
    .fullPrice {
        text-align: center;
    }
</style>


