<template>
    <div class="content">
        <el-row :class="{'row-bg': true}" type="flex">
            <el-steps :space="size" :center="true" :align-center="true" :active="active" finish-status="success">
                <el-step title="Intro"></el-step>
                <el-step title="Lanche"></el-step>
                <el-step title="Outros"></el-step>
                <el-step title="Confirmar"></el-step>
            </el-steps>
            <!--el-steps direction="vertical" :space="size" :active="active" finish-status="success" >
                <el-step title="Intro" :class="{'stepper': true}"></el-step>
                <el-step title="Lanche" :class="{'stepper': true}"></el-step>
                <el-step title="Outros"></el-step>
                <el-step title="Confirmar"></el-step>
            </el-steps-->
            <keep-alive>
                <component class="step" :is="steps" :lanches="lanches" :outros="outros"></component>
            </keep-alive>
            
            <el-col>
                <div class="buttons">
                    <el-button :class="{btnLanches: true}" @click="back">Voltar</el-button>
                    <el-button v-if="active != 3 " type="primary" :class="{btnLanches: true, 'blue-bg': true}" @click="next">Continuar</el-button>
                    <el-button v-else type="success" :class="{btnLanches: true, 'blue-bg': true}" @click="">Concluir</el-button>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
    import Step1 from './Lanches/Step1';
    import Step2 from './Lanches/Step2';
    import Step3 from './Lanches/Step3';
    import Step4 from './Lanches/Step4';


     import axios from 'axios'; 
    export default {
        data() {
            return {
                active: 0,
                size: 85,
                lanches: '',
                outros: ''
            }
        },
        computed: {
            steps() {
                return 'Step' + (this.active + 1);
            }
        },
        mounted() {
            this.request();
        },
        methods: {
            next() {
                if (this.active++ > 3) this.active = 0;
            },
            back() {
                if (this.active-- < 1) this.active = 0;
            },
            request() {
                axios.post("api/responsavel/lanches")
                    .then(response => {

                        const dados = response.data;
                        //console.log('response');
                        //console.log(dados);
                        this.lanches = dados.lanches;
                        this.outros = dados.outros;
                        /*
                        response é um json com uma lista de objetos.
                        response = {
                           lanches: [
                                {
                                    id: 'codigo',
                                    name: 'nome',
                                    price: 'preço unitário'
                                }
                            ],
                            outros: [
                                {
                                    id: 'codigo',
                                    name: 'nome',
                                    price: 'preço unitário'
                                }
                            ]
                        }
                        */

                    })
                    .catch(error => {
                        console.log('error');
                        console.log(error);
                    })
            },
        },
        components: {
            Step1,
            Step2,
            Step3,
            Step4
        }
    }
</script>
<style scoped>
    .buttons {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .step {
         min-height: 60vh;
         margin-top: 15px;
    }
    .content {
        height: 100vh;
        display: flex;
        justify-content: center;
    }

    .row-bg {
        flex-direction: column;
        margin-top: 3vh;
        align-items: center;
    }

    .btnLanches {
        margin-top: 12px;
        font-family: 'Roboto', sans-serif;
    }

    @media (max-width: 768px) {
        .buttons {
            width: 100vw;
        }
    }
    
</style>


